import React from 'react';
import {inject, observer} from 'mobx-react';
import {Breadcrumb, Row, Col, Form, Select, message} from 'antd';
import ManualStore from '@/store/report/ManualStore';
import moment from 'moment';
import url from 'url';
import ManualModalOne from '@/components/report/ManualModalOne';
import ManualModalTwo from '@/components/report/ManualModalTwo';

import DatePicker from 'bee-datepicker';
import 'bee-datepicker/build/DatePicker.css';
import zhCN from "bee-datepicker/build/locale/zh_CN";
const { YearPicker } = DatePicker;


//const {MonthPicker} = DatePicker;
const {Option} = Select;
const obj = {
	add: '新增',
	edit: '编辑',
	view: '查看'
}

@Form.create()
@observer
class ManualReporting extends React.Component {
	constructor(props) {
		super(props);
		this.store = new ManualStore();
		this.state = {
			reportTypeList: [],
			reportNameList: [],
			value: moment().format("YYYYMM"),
			name: '', // 报表名称
			title: '', // 表单状态
		}
	}

	componentDidMount() {
		const {form} = this.props;
		let search = this.props.location.search;
		const {title, classifyName, theme, month} = url.parse(search, true).query;
		if (title=="add") { // 新增报表
			this.getReportType();
			// 填报月份
			this.setState({
				title: title
			})
		}
		if (title=="edit" || title=="view") {  // 编辑报表、查看
			// 查询类别
			//console.log(url.parse(search, true).query)
			const params = {
				"level": 1,
				"parentId": ""
			}
			this.store.getReportClassifys(params).then((res) => {
				if (res) {
					this.setState({
						reportTypeList: Object.assign([], res.filter((res) => {   // 暂时控制财务和营销的数据
							return res.name == "财务" || res.name == "营销";
						}))
					}, ()=>{
						// 查询名称
						const id = this.state.reportTypeList.filter((item)=>{return item.name===theme})&&this.state.reportTypeList.filter((item)=>{return item.name===theme})[0]['id'];
						//this.getReportName(id);

						const {form} = this.props;
						const params = {
							"level": 2,
							"parentId": id
						}
						this.store.getReportClassifys(params).then((res) => {
							if (res) {
								let tempData = res;
								if (id == "1") { // 财务  暂时只做6张表
									this.setState({
										reportNameList: Object.assign([], tempData.filter((item) => {
											return item.name == "基础费用表" || item.name == "人力成本表" || item.name == "专项费用表" || item.name == "营业成本表" || item.name == "营业收入表";
										}))
									}, () => {
										form.setFieldsValue({["reportType"]: theme});
										form.setFieldsValue({["reportName"]: classifyName});

										this.setState({
											name: classifyName
										});

										// 初始查询
										this.getYearTarget(classifyName);
									});
								} else if (id == "2") {
									this.setState({
										reportNameList: Object.assign([], tempData.filter((item) => {
											return item.name == "合同情况表";
										}))
									}, () => {

										form.setFieldsValue({["reportType"]: theme});
										form.setFieldsValue({["reportName"]: classifyName});

										this.setState({
											name: classifyName
										});
										// 初始查询
										this.getYearTarget(classifyName);
									});
								}
							}
						});

					});
				}
			});

			// 填报月份
			this.setState({
				value: month,
				title: title
			})
		}
	}

	// 报表分类
	getReportType() {
		const params = {
			"level": 1,
			"parentId": ""
		}
		this.store.getReportClassifys(params).then((res) => {
			if (res) {
				this.setState({
					reportTypeList: Object.assign([], res.filter((res) => {   // 暂时控制财务和营销的数据
						return res.name == "财务" || res.name == "营销";
					}))
				})
			}
		});
	}

	// 报表名称
	getReportName(parentId) {
		const {form} = this.props;
		const params = {
			"level": 2,
			"parentId": parentId
		}
		this.store.getReportClassifys(params).then((res) => {
			if (res) {
				let tempData = res;
				if (parentId == "1") { // 财务  暂时只做6张表
					this.setState({
						reportNameList: Object.assign([], tempData.filter((item) => {
							return item.name == "基础费用表" || item.name == "人力成本表" || item.name == "专项费用表" || item.name == "营业成本表" || item.name == "营业收入表";
						}))
					}, () => {
						form.setFieldsValue({["reportName"]: this.state.reportNameList[0]['name']});
						this.setState({
							name: this.state.reportNameList[0]['name']
						});

						// 初始查询
						this.getYearTarget(this.state.reportNameList[0]['name']);
					});
				} else if (parentId == "2") {
					this.setState({
						reportNameList: Object.assign([], tempData.filter((item) => {
							return item.name == "合同情况表";
						}))
					}, () => {
						form.setFieldsValue({["reportName"]: this.state.reportNameList[0]['name']});
						this.setState({
							name: this.state.reportNameList[0]['name']
						});
						// 初始查询
						this.getYearTarget(this.state.reportNameList[0]['name']);
					});
				}
			}
		});
	}

	handleChange = (value, e) => {
        const id = e.props.val.id;
		this.getReportName(id);
	}

	disabledDate = (current) => {
		return current && current > moment().endOf('date');
	}

	/*handleDownload = (value, dateString) => {
		this.setState({
			value: dateString
		});
	}*/

	onYearChange = (d, dataString) => {
		// 获取当前年份
		const year = moment().year();
		if (dataString > year) {  //比较当前
			message.warn("请选择今年以前的年份")
            return false;
		} else {
			// 查询
			this.setState({
				value: dataString
			});


		}
	};

	// 查询
	getYearTarget = (param) => {
		switch (param) {
			case '人力成本表':
				const params = {
					yearName: moment(this.state.value+"11").format("YYYY"),
					tableName:'rlcbztqk',
					schemaName:'yearCost',
					keyName:'hr',
				}
				this.store.getYearTarget(1, {yearTarget:params});
				break;
			case '基础费用表':
				const param2 = {
					yearName: moment(this.state.value+"11").format("YYYY"),
					tableName:'jcfyztqk',
					schemaName:'yearCost',
					keyName:'',
				}
				this.store.getYearTarget(1, {yearTarget:param2});
				break;
			case '专项费用表':
				const param3 = {
					yearName: moment(this.state.value+"11").format("YYYY"),
					tableName:'zxfyztqk',
					schemaName:'yearCost',
					keyName:'',
				}
				this.store.getYearTarget(1, {yearTarget:param3});
				break;
			case '营业成本表':
				const param4 = {
					yearName: moment(this.state.value+"11").format("YYYY"),
					tableName:'yycb',
					schemaName:'area',
					keyName:'',
					keyValue:'',
				}
				this.store.getYearTarget(2, {yearTarget:param4});
				break;
			case '营业收入表':
				const param5 = {
					yearName: moment(this.state.value+"11").format("YYYY"),
					tableName:'yysr',
					schemaName:'area',
					keyName:'',
					keyValue:'',
				}
				this.store.getYearTarget(2, {yearTarget:param5});
				break;
			case '合同情况表':
				const param6 = {
					yearName: moment(this.state.value+"11").format("YYYY"),
					tableName:'htqk',
					schemaName:'area',
					keyName:'',
					keyValue:'',
				}
				this.store.getYearTarget(2, {yearTarget:param6});
				break;
			default:
					break;
		}
	}

	// 选择报表名称查询
	handleReportName = (value, e) => {
		const param = e.props.val.name;
		this.setState({
			name: param
		});
		this.getYearTarget(param);
	}

	goBack = () => {
		this.props.history.goBack();
	}

	render() {
		const {reportTypeList, reportNameList, value, name, title} = this.state;
		const {getFieldDecorator} = this.props.form;
		const formItemLayout = {
			labelCol: {
				xs: {span: 24},
				sm: {span: 6},
			},
			wrapperCol: {
				xs: {span: 24},
				sm: {span: 16},
			},
		};

		return (
			<div>
				<div className="con_bottom">
					<Breadcrumb separator=">" style={{margin: '0'}}>
						<Breadcrumb.Item>系统管理</Breadcrumb.Item>
						<Breadcrumb.Item href="#/report">报表管理</Breadcrumb.Item>
						<Breadcrumb.Item>{obj[title]}报表</Breadcrumb.Item>
					</Breadcrumb>
				</div>
				<Form layout="inline" {...formItemLayout} onSubmit={this.handleSubmit}>
					<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 8}, 15]} style={{"marginTop": "15px"}}>
						<Col className="gutter-row" xs={24} sm={24} md={18} lg={12} xl={8}>
							<Form.Item label="报表类别" className="w">
								{getFieldDecorator('reportType', {
									rules: [{required: false, message: '请选择报表类别'}],
								})(
									<Select
										placeholder="请选择"
										className="w"
										style={{width: '250px'}}
										onChange={this.handleChange}>
										{
											reportTypeList.map((item, index) => {
												return (<Option key={index} value={item.name} val={item}>{item.name}</Option>)
											})
										}
									</Select>
								)}
							</Form.Item>
						</Col>
						<Col className="gutter-row" xs={24} sm={24} md={18} lg={12} xl={8}>
							<Form.Item label="报表名称"  className="w">
								{getFieldDecorator('reportName', {
									rules: [{required: false, message: '请选择报表名称'}],
								})(
									<Select
										placeholder="请选择"
										className="w"
										style={{width: '250px'}}
										onChange={this.handleReportName}>
										{
											reportNameList.map((item, index) => {
												return (<Option key={index} value={item.name} val={item}>{item.name}</Option>)
											})
										}
									</Select>
								)}
							</Form.Item>
						</Col>
						<Col className="gutter-row" xs={24} sm={24} md={18} lg={12} xl={8}>
							<Form.Item label="填报年份" className="w">
								{getFieldDecorator('reportMonth', {
									initialValue: moment(value, 'YYYYMM'),
									rules: [{required: false, message: '请选择填报年份'}],
								})(
									<YearPicker
										className="w manna_year"
										style={{width: '250px'}}
										format="YYYY"
										onChange={this.onYearChange}
										locale={zhCN}
										placeholder="选择年"
										defaultValue={moment()}
										showClose={false}
									/>
								)}
							</Form.Item>
							{/*<MonthPicker
										className="w"
										style={{width: '250px'}}
										disabledDate={this.disabledDate}
										onChange={this.handleDownload}
										placeholder="请选择"/>*/}
						</Col>
					</Row>
				</Form>
				{name=="人力成本表"||name=="基础费用表"||name=="专项费用表"?<ManualModalOne
					name={name}
				    store={this.store}
					title={title}
					goBack={this.goBack}
				/>:null}
				{name=="营业成本表"||name=="营业收入表"||name=="合同情况表"?<ManualModalTwo
					name={name}
					store={this.store}
					title={title}
					goBack={this.goBack}
				/>:null}
			</div>
		)
	}
}


export default ManualReporting;




